@charset "utf-8";
.newitem{
  padding-top: 1px;
  width: 1050px;
  height: 2200px;
  margin: 0px auto;
}
.newmenu{
  position: relative;
  
}
.newitem h3{
  margin-top: 50px;
  text-align: center;
  font-size: 28px;
  color: #333;
}
.newitem .filter{
  position: absolute;
  width: 220px;
  height: 300px;
  top: 37px;
  left: 0px;
  padding-top: 40px;
}
.newitem .filter li{
  font-size: 15px;
  color: #333;
  height: 52px;
  line-height: 52px;
  font-weight: bold;
  border-bottom: 1px solid #ccc;
}
.newitem .product{
  width: 783px;
  float: right;
}
.newitem .product .sort ul{
  position: relative;
  display: flex;
  justify-content: end;
  padding-bottom: 20px;
}
.newitem .product .sort li{
  color: #ccc;
  font-size: 14px;
}
.newitem .product .sort li::after{
  content: "|";
  margin: 0px 10px 0px 10px;
}
.newitem .product .sort li:first-child{
  position: absolute;
  left: 0px;top: 0px;
  color: #333;
  font-weight: bold;
}
.newitem .product .sort li:last-child:after,.newitem .product .sort li:first-child:after{
  display: none;
}
/* 상품 */
.newitem .item{
  width: 249px;height: 538px;
  text-align: left;
}
.newitem .pro{
  width: 783px;
  margin: 0px auto;
  padding: 40px 0px;
}
.newitem .product_container{
  display:flex;
  justify-content: space-between;
  flex-direction: row;
  width: 783px;
  flex-wrap : wrap;
}
.newitem .product{
  width: 783px;
  padding: 40px 0px;
}
.newitem .product_list{
  display: flex;
  justify-content: space-between;
  flex-wrap : wrap;
}
.newitem .product div{
  width: 783px;
  height: 48px;
}
.newitem .product h3{
  color: #333;
  text-align: center;
  font-size: 28px;
  padding: 8px 0px 8px 0px;
}
.newitem .item .name{
  width: 249px;
  font-size: 16px;
  color: #333;
  margin: 0px 0px 8px;
}
.newitem .delivery{
  padding-top: 14px;
  color: #999;
  font-size: 14px;
}
.newitem .item img{
  width:249px;
  height:320px;
  transition: 0.4s;
  overflow: hidden;
}
.newitem .item img:hover{
  filter: brightness(0.5);
}
.newitem .item .price{
  font-size : 16px;
  color: #333;
  font-weight: 1000;
}