.item{
  width: 1050px;height: 434px;
  text-align: left;
}
.pro{
  width: 1050px;
  margin: 0px auto;
  padding: 40px 0px;
}
.pro h2{
  width: 1050px;
  color: #333333;
  font-size: 28px;
  text-align: center;
  margin-bottom: 27px;
}
.product_container{
  display:flex;
  justify-content: space-between;
  flex-direction: row;
  width: 1050px;
  flex-wrap : wrap;
}
.product{
  width: 1050px;
  padding: 40px 0px;
}
.product_list{
  display: flex;
}
.product div{
  width: 1050px;
  height: 48px;
  margin-bottom: 27px;
}
.product h3{
  color: #333;
  text-align: center;
  font-size: 28px;
  padding: 8px 0px 8px 0px;
}
.item .name{
  width: 249px;
  font-size: 16px;
  color: #333;
  margin: 0px 0px 8px;
}
.item img{
  width:249px;
  height:320px;
  transition: 0.4s;
  overflow: hidden;
}
.item img:hover{
  filter: brightness(0.5);
}
.item .price{
  font-size : 16px;
  color: #333;
  font-weight: 1000;
}
.sub-banner-image{
  padding: 40px 0px;
}