@charset "utf-8";
*{margin:0px;padding:0px;}
ul, li, ol{list-style:none;}
body{
  font-family: "맑은 고딕",arial, sans-serif;
  font-size:12px;
  color:#333;
}
a{text-decoration:none;color:#333;}

header{
  width:100%;height:156px;
  box-shadow:0px 0px 3px #333;
}
.header_inner{
  width:1050px;height:53px;
  margin:0px auto;
}
.login ul{
  padding-top: 10px;
  display: flex;
  justify-content: end;
}
.login ul li{
  font-size: 12px;
}
#join{
  color: #5f0080;
}
#join::after,#login::after{
  content: "|";
  margin-left: 10px;
  margin-right: 10px;
}

.h_top{
  padding-top: 14px;
  width:1050px;height:53px;
  margin:0px auto;
  display: flex;
  justify-content: space-between;
}
.logo {
  display: flex;
}
#logo{
  margin-top: 8px;
}
h1 span{
 position: relative;
 bottom: 14px;
}
#market{
  margin-left: 20px;
  font-size: 17px;
  color: rgba(95, 0, 128, 1);
}
#beauty{
  margin: 0px;
  color: #ccc;
  font-size: 17px;
}
#beauty:hover{
  color: rgba(95, 0, 128, 1);
}
#line{
  font-size: 13px;
  font-weight: lighter;
  color: #ccc;
  margin: 0px 10px 0px 10px;
  position: relative;
  bottom: 16px;
}
header input{
  width: 400px;
  height: 48px;
  border-radius: 5px;
  border: 1px solid #5f0080;
  font-size: 15px;
  padding: 6px 72px 6px 14px;
  box-sizing: border-box;
}
header input::placeholder{
  color: #ccc;
}
.search{
  position: relative;
}
#searchicon img{
  position: absolute;
  width: 35px;
  right: 10px;
  top: 8px;
  font-size: 25px;
  color: #5f0080;
  
}
.icons img{
  width: 36px;
  padding: 8px 10px 0px;
}
.icons img:hover{
}
/* gnb */
.gnb ul{
  display: flex;
  justify-content: space-between;
}
.gnb ul li{
  text-align: center;
  width: 150px;
  height: 56px;
  font-size: 16px;
  color: #333;
  font-weight: bold;
  margin-top: 25px;
}
.b_menu span:hover{
  color: #5f0080;
  border-bottom: 1px solid #5f0080;
}
#category{
  margin-right: 60px;
  text-align: left;
  padding-left: 30px;
  position: relative;
}
.category_t{
  margin-left: 30px;
}
#category:hover{
  color: #5f0080;
}
#menuicon{
  position: absolute;
}
#menuicon img{
  width: 16px;
  top: 0px;
  left: 0px;
}
#delivery{
  margin-left: 100px;
  float: right;
}
#delivery div{
  box-sizing: border-box;
  font-size: 13px;
  color: #666666;
  width: 121px; height: 31px;
  border: 1px solid #ccc;
  border-radius: 20px;
  padding-top: 5px;
  transform: translateY(-5px);
}
#delivery span {
  color: #5f0080;
}


/* footer */
footer{
  width:100%;height:580px;
  padding-top:30px;
  border-top:1px solid #ccc;
}
.f_inner{
  width:1050px;
  margin:0px auto;
}
.f_top{
  height: 368px;
  margin-bottom: 30px;
 text-align: left;
}
.f_l{
  width: 440px;
  height: 368px;
  float: left;
}
.f_l h2{
  margin: 29px 0px 17px;
  color: #333;
  font-size: 20px;
}
.f_l .number{
  margin-bottom: 20px;
  color: #4C4C4C;
  font-size: 28px;
}
.f_l .number span{
  font-size: 16px;
  padding-left: 4px;
}
.f_box{display: flex;}
.f_l .box{
  box-sizing: border-box;
  padding-top: 10px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 140px;
  height: 40px;
  color: #333;
  font-size: 14px;
  margin: 0px 16px 16px 0px;
}
.b_text{
  color: #999999;
  margin: 2px 0px 0px;
}
.f_l p{
  color: #999999;
  font-size: 12px;
}
.f_r{
  width: 480px;
  height: 368px;
  float: right;
}
.f_r ul{
  display: flex;
  margin: 29px 0px 29px;
}
.f_r li:nth-child(5){
  font-weight: bold;
}
.f_r li{
    color: #333;
    font-size: 13px;
    padding-right: 14px;
    font-weight: bold;
}
.f_r .link{
  font-weight: bold;
  color: #5f0080;
}
.sns{
  display: flex;
}
.sns *{
  width: 30px;
  height: 30px;
  margin-top: 30px;
  margin-right: 10px;
}
.f_link{
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #ccc;
}
.f_link li{
  margin: 26px 0px 33px;
  display: flex;
}
.f_link li img{
  height: 34px;
  margin-right: 10px;
}
.f_link li .txt{
  width: 167px;
  font-size: 10px;
  color: #999999;
}
.f_bottom{
  padding-top: 19px;
  padding-bottom: 27px;
  height:103px;
  background: #f7f7f7;
}
.txt1{
  color: #999999;
  font-size: 10px;
  text-align: center;
  margin-bottom:5px ;
}
address{
  font-style: normal;
  color: #999999;
  font-size: 10px;
  text-align: center;
}