@charset "utf-8";
#loginpage{
  width: 1050px;
  margin: 0px auto;
  height: 345px;
  background-color: #fff;
  padding-bottom: 60px;
}
#loginpage h2{
  text-align: center;
  font-size: 20px;
margin-bottom: 40px; margin-top:90px;
}
#loginpage input{
  margin: 0px auto;
  display: block;
  width:340px;
  height: 54px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 13px;
  padding: 0px 11px 1px 15px;
  box-sizing: border-box;
  margin-bottom: 12px;
}
#loginpage input::placeholder{
  color: rgb(112, 112, 112);
}
#loginpage #loginform{
  background-color: #5F0080;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
}
#loginpage #joinform{
  background-color: #fff;
  border: 1px solid #5F0080;
  font-size: 16px;
  font-weight: bold;
  color: #5F0080;
}

#find{
  margin: 0px auto;
  width: 340px;
  height: 35px;
  line-height: 15px;
  text-align: right;
}
#find li{
  display: inline-block;
}
#find_id::after{
  content: "|";
  margin: 0px 5px 0px 5px;
}