@charset "utf-8";
* {
  margin: 0px;padding: 0px;
  list-style: none;
}
.slide-wrapper{
  width: 1900px;
  height: 370px;
  margin: 0px auto;
  position: relative;
  overflow: hidden;
}
.slide{
  width: 100%;
  display: flex;
  position: absolute;
  left: 0px;
  top: 0px;
  transition: all 0.3s;
}
.slide >li{
  width: 100%; height: 370px;
}
.slide >li:not(:last-child){
  margin-right: 30px;
}
.slide >li>div{}
.top_group{
  width: 100%;
  height: 370px;
  position: relative;
}
.top_group >img{
position: absolute;
font-size: 16px;
z-index: 10;
}
/* .top_group:hover +.prev,.top_group:hover +.next{
  display: block;

} */
.prev,.next{
  /* display: none; */
  transition: 1s;
  width: 52px;
  height: 52px;
}
.prev{
  transform: scaleX(-1);
  left: 290px; top: 159px;
}
.next{right: 290px; top:159px;}
